.App {
    box-sizing: border-box;
    padding: 24px 32px;
    font-family: 'Open Sans', sans-serif;
    display: flex;
    flex-direction: column;
    height: 100vh;
}

.chat {
    margin-top: 15px;
}

.chat.me {
    text-align: right;
}

.chat.me .box {
    background-color: #1bafc2;
    color: white;
}

.chat .box {
    background-color: #f2f2f2;
    display: inline-block;
    padding: 6px 12px;
    border-radius: 6px;
}

.chat-container {
    flex: 1;
    overflow: auto;
    padding-bottom: 64px;
}

.input-container {
    padding: 8px 12px;
    width: 100%;
    box-sizing: border-box;
    box-shadow: 0 3px 6px rgba(0, 0, 0, .12);
    border: 1px solid #eaeaea;
    border-radius: 9px;
}

.input-container input {
    width: 100%;
    height: 27px;
    border: none;
    font-size: 15px;
    outline: none;
    font-weight: 600;
    font-family: 'Open Sans', sans-serif;
}

body {
    font-family: 'Open Sans', sans-serif;
}